/*
This file is used to display wave on the left side of the 
screen on the soectrum page. This is done with the 
use of paperJS and AudioContext. We will look for the 
access to the users microphone, then for each (1/sampleRate) 
seconds we will pull in the current audio value. Theses vales 
are place in a buffer witch are then put on the graph to show 
the current value of buffer.
*/

import Paper from "paper";
import exportCSV from 'components/exportCSV';
import {clearOldWave, getMicPermission} from 'components/DrawOscopeWave';
let fft = require('fft-js').fft;
let windowing = require('fft-windowing');

const mobile = false;
let sampleRate = 44100;
let drawWaveInterval;
let dataArray;

//.707 VRMS = -1 to 1 V AC
//No resistance (no adapter): 149 Hz
//With resistance (adapter): 91 Hz
const voltageMultiplierConstant = .5 * .9;

// Use Effects
let windowType;
let center;
let autoset;
let drawingWave;
let decibelsDiv;
let freqDiv;
let saveFlag;
let fftSize;
export const setSpectrumDrawWave = (val) => drawingWave = val;
export const setDecibelsDiv = (val) => decibelsDiv = val;
export const setFreqDiv = (val) => freqDiv = val;
export const setSpectrumAutoset = (val) => autoset = val;
export const setSpectrumSaveFlag = (val) => saveFlag = val;
export const setWindowType = (val) => windowType = val;
export const setCenter = (val) => center = val;
export const setFFTSize = (val) => fftSize = val;

export const drawSpectrumWave = (autosetFinishCallback, freqDivChangeCallback, centerChangeCallback, saveFinishCallback) => {
  clearOldWave(drawWaveInterval);

  // Input buffer
  dataArray = new Float32Array(fftSize);

  if (window.audioinput) {
    getMicPermission();

    // Mobile Only
    window.addEventListener("audioinput", (evt) => {
      if(drawingWave) {
        if (fftSize <= 4096) {
          dataArray = new Float32Array(fftSize);
        }
        dataArray = evt.data;
        dataArray = performFFTSubset(dataArray, fftSize);
        performFFT(dataArray, windowType);
      }
    });
  } else {
    var constraints = { audio: {autoGainControl: false, echoCancellation: false,  noiseSuppression: false}};
    navigator.mediaDevices.getUserMedia(constraints)
    .then(function(mediaStream) {
      let ctx = new AudioContext({sampleRate: 44100});
      sampleRate = ctx.sampleRate;
      let mic = ctx.createMediaStreamSource(mediaStream);
      let spe = ctx.createAnalyser();
      spe.fftSize = fftSize;
      mic.connect(spe);

      let osc = ctx.createOscillator();
      osc.type = 'sine';
      osc.frequency.value = 440;

      drawWaveInterval = setInterval(() => {
        // Skip if not drawing
        if(drawingWave) {
          dataArray = new Float32Array(fftSize);
          spe.getFloatTimeDomainData(dataArray);
          performFFT(dataArray, windowType);
        }
      }, 20);
    })
    .catch(function(err) { console.log(err.name + ": " + err.message); }); // always check for errors at the end.
  }

  function Wave() {
    this.amount = 200;
    this.height = 10;
    this.offsetX = 0;
    this.waveSpeed = 3;
    this.selected = false;

    this._path = new Paper.Path({ fillColor: this.color });

    this.init = function () {
      this._path.strokeColor = "#D00100";
      this._path.selected = this.selected;

      this._path.segments = [];

      // Cover entire horizontal screen space with points
      for (let i = 0; i <= this.amount; i++) {
        let point = new Paper.Point(i / this.amount * Paper.view.size.width,
          1 / 2 * Paper.view.size.height);
        this._path.add(point);
      }
    }

    this.update = function (_event) {
      let height = Paper.view.size.height;
      let freqPerPoint = sampleRate / fftSize;

      if (autoset) {
        let maxDecibels = Number.NEGATIVE_INFINITY;
        let maxDecibelsIdx = Number.NEGATIVE_INFINITY;
        for (let i = 0; i < dataArray.length; i++) {
          if (dataArray[i] > maxDecibels) {
            maxDecibels = dataArray[i]
            maxDecibelsIdx = i;
          }
        }
        let newCenterFrequency = maxDecibelsIdx * freqPerPoint;
        autoset = false;
        centerChangeCallback(newCenterFrequency);
        autosetFinishCallback();
      }

      if (saveFlag) {
        exportCSV(dataArray, mobile);
        saveFlag = false;
        saveFinishCallback();
      }

      // Graph 200 points around the center frequency
      let centerIdx = Math.floor(center / freqPerPoint);
      let leftIdx = centerIdx - (100 * freqDiv);

      // Get heights of canvas elements
      const divHeight = height / 8;
      const zeroHeight = height / 4;

      for (let i = 0; i <= this.amount; i++) {
        let bufferPoint = dataArray[freqDiv * i + leftIdx]; // freqDiv is a number between [1-20]
        
        // Flip sign of bufferPoint because more positive numbers show up lower on the canvas
        bufferPoint = bufferPoint * -1;
        bufferPoint /= decibelsDiv; 

        // Set the y coordinate of the ith segment point
        let segment = this._path.segments[i];
        segment.point.y = zeroHeight + (divHeight * bufferPoint);
      }
    }
  }

  let waveLayer = new Paper.Layer();

  var path1 = new Wave();
  path1.color = 'blue';
  path1.height = 10;
  path1.init();

  Paper.view.on('frame', function (event) {
    waveLayer.activate();
    path1.update(event);
  });
  
  Paper.view.on('resize', function (event) {
    waveLayer = new Paper.Layer();
    waveLayer.activate();
    path1.init();
  });
};


// -------------------------------------------- Helper Functions ----------------------------------------------------

function performFFT (array, window) {
  if (window === "Hanning") {
    array = windowing.hann(array);
  } else if (window === "Gaussian") {
    array = windowing.gaussian(array);
  } else if (window === "Hamming") {
    array = windowing.hamming(array);
  } else if (window === "Kaiser") {
    array = windowing.kaiser(array);
  } else if (window === "Flat Top") {
    array = windowing.flat_top(array);
  }
  let response = fft(array);
  // Populates array with the magnitude of the FFT result
  let k = 1;
  let n = array.length;
  for (let i = 0; i < n; i++) {
    let magnitude = Math.sqrt( Math.pow(response[i][0], 2) + Math.pow(response[i][1], 2) );
    array[i] = 20 * Math.log10( Math.abs((k/n) * magnitude) ); 
    k = Math.sqrt(2)
  }
}

function performFFTSubset (fullArray, newSize) {
  let subsetArray = new Float32Array(newSize);
  let fullSize = fullArray.length;
  let iIncrement = Math.ceil(fullSize/newSize);
  let i = 0;
  let j = 0;
  while (i < fullSize) {
    subsetArray[j] = fullArray[i];
    j++;
    i += iIncrement;
  }
  // Mobile Only. Dataset only contains 4095 values. Add one more from the fullArray.
  if (fullSize === 4095) {
    subsetArray[newSize - 1] = fullArray[fullSize - 1];
  }
  return subsetArray;
}
