/*
  About: This file is used to create the cursors for the oscilloscope. This is done
  with the use of PaperJS. How it all works is when the hit box is moved the cursor 
  (the circle and dashed line) will also move. Based of the position of dashed line,
  the position value will be updated and the difference will be taken to display to 
  the user. The values that are exported from this file are just the difference 
  between the time and voltage cursors. All the math is done within wave.js.
*/

import Paper from "paper";
import React from "react";
import { clamp } from "utils/MathUtils";

enum CursorDirection {
  eUndefined,
  eVertical,
  eHorazontal
}

class Cursor{
  protected _direction: CursorDirection = CursorDirection.eUndefined;
  protected _position: number = 0;

  protected _line : any;
  protected _cursor : any;

  protected _active: any = null;

  constructor(){}

  getLine() { return this._line; }
  getCursor() { return this._cursor; }
  getDirection() : CursorDirection { return this._direction; }

  getActive(): any { return this._active; }

  setPosition(position: number) { this._position = position; }
  getPosition() : number { return this._position; }

  addTolayer(layer: any){
    layer.addChildren(this._line);
    layer.addChildren(this._cursor);
  }
  
  setVisible(visible: boolean){
    this._cursor.visible = visible;
    this._line.visible = visible;
  }

  onMouseDown(event: any, hitOptions: any){
    let hitResult = this._cursor.hitTest(event.point, hitOptions);
    this._active = hitResult && hitResult.item;
  }

  onMouseUp(event:any ){
    this._active = false;
  }

  onMouseDrag(event:any ){}

  onResize(event: any, cursorFlag: boolean){
    this._cursor.remove();
    this._line.remove();
  }
}

class VerticalCursor extends Cursor{
  constructor(layer: any, position: number){
    super();
    this._direction = CursorDirection.eVertical;

    this._position = position;
    this._init();

    this._line.strokeColor = 'Black';
    this._line.strokeWidth = 3;
    this._line.strokeCap = 'round';
    this._line.dashArray = [10, 12];

    this.addTolayer(layer);
    this.setPosition(position);
  }

  private _init(){
    this._line = new Paper.Path({
      segments: [
        [Paper.view.bounds.width * this._position, 0], 
        [
          Paper.view.bounds.width * this._position, 
          Paper.view.bounds.height * 2
        ]
      ],
      selected: false
    });

    this._cursor = new Paper.Path.Circle({
      center: Paper.view.center,
      radius: 15,
      fillColor: 'red'
    });
  }

  setPosition(position: number){
    super.setPosition(position);
    this._line.position.x = Paper.view.bounds.width * position;
    this._line.position.y = Paper.view.bounds.height;
    this._cursor.position.x = Paper.view.bounds.width * position;
    this._cursor.position.y = Paper.view.bounds.height;
  }

  onMouseDrag(event: any){
    if(!this.getActive()) return;

    this.setPosition(clamp(
      event.point.x / Paper.view.bounds.width, 
      0.0, 
      1.0
    ));
    this._active.position.x = this._line.position.x;
  }

  onResize(event: any, cursorFlag: boolean){
    super.onResize(event, cursorFlag);

    // Regenerate the cursor
    this._init();
    
    this._line.strokeColor = 'Black';
    this._line.strokeWidth = 3;
    this._line.strokeCap = 'round';
    this._line.dashArray = [10, 12];

    this.setPosition(this.getPosition());
    this.setVisible(cursorFlag);
  }
}

class HorazontalCursor extends Cursor{
    constructor(layer: any, position: number){
      super();
      this._direction = CursorDirection.eHorazontal;

      this._position = position;
      this._init();

      this._line.strokeColor = 'Black';
      this._line.strokeWidth = 3;
      this._line.strokeCap = 'round';
      this._line.dashArray = [10, 12];

      this.addTolayer(layer);
      this.setPosition(position);
    }

    private _init(){
      this._line = new Paper.Path({
        segments: [
          [0, Paper.view.bounds.height * (1-this._position)], 
          [
            Paper.view.bounds.width * 2, 
            Paper.view.bounds.height * (1-this._position)
          ]
        ],
        selected: false
      });

      this._cursor = new Paper.Path.Circle({
        center: Paper.view.center,
        radius: 15,
        fillColor: 'red'
      });
    }

    setPosition(position: number){
      super.setPosition(position);
      this._line.position.x = Paper.view.bounds.width;
      this._line.position.y = Paper.view.bounds.height * (1-this._position);
      this._cursor.position.x = Paper.view.bounds.width;
      this._cursor.position.y = Paper.view.bounds.height * (1-this._position);
    }

    onResize(event: any, cursorFlag: boolean){
      super.onResize(event, cursorFlag);

      // Regenerate Line
      this._init();
    
      this._line.strokeColor = 'Black';
      this._line.strokeWidth = 3;
      this._line.strokeCap = 'round';
      this._line.dashArray = [10, 12];

      this.setPosition(this.getPosition());
      this.setVisible(cursorFlag);
    }
  
  onMouseDrag(event: any){
    if(!this.getActive()) return;

    this.setPosition(clamp(
      1 - (event.point.y / Paper.view.bounds.height), 
      0.0, 
      1.0
    ));
    this._active.position.y = this._line.position.y;
  }
}

let cursorFlag = false;
export const setCursorFlag = (val:any) => cursorFlag = val;

const drawCursors = (onUpdate: (timeDiff: number, voltDiff: number) => void) => {
  let cursorLayer = new Paper.Layer();

  let timeCursor1 = new VerticalCursor(cursorLayer, .25);
  let timeCursor2 = new VerticalCursor(cursorLayer, .75);
  let voltageCursor1 = new HorazontalCursor(cursorLayer, .25);
  let voltageCursor2 = new HorazontalCursor(cursorLayer, .75);

  let timeCursorDiff = Math.abs(timeCursor1.getPosition() - timeCursor2.getPosition());
  let voltCursorDiff = Math.abs(voltageCursor1.getPosition() - voltageCursor2.getPosition());

  timeCursor1.setVisible(cursorFlag);
  timeCursor2.setVisible(cursorFlag);
  voltageCursor1.setVisible(cursorFlag);
  voltageCursor2.setVisible(cursorFlag);

  onUpdate(timeCursorDiff, voltCursorDiff);

  // Move cursor using touch
  let hitOptions = {
    stroke: true,
    fill: true,
    tolerance: 50,
  }

  Paper.view.on('mousedown', function(event: any) {
    timeCursor1.onMouseDown(event, hitOptions);
    timeCursor2.onMouseDown(event, hitOptions);
    voltageCursor1.onMouseDown(event, hitOptions);
    voltageCursor2.onMouseDown(event, hitOptions);
  });

  Paper.view.on('mousedrag', function(event: any) {    
    if(timeCursor1.getActive()){
      timeCursor1.onMouseDrag(event);
    }else if(timeCursor2.getActive()){
      timeCursor2.onMouseDrag(event);
    }else if(voltageCursor1.getActive()){
      voltageCursor1.onMouseDrag(event);
    }else if(voltageCursor2.getActive()){
      voltageCursor2.onMouseDrag(event);
    }

    let timeCursorDiff = Math.abs(timeCursor1.getPosition() - timeCursor2.getPosition());
    let voltCursorDiff = Math.abs(voltageCursor1.getPosition() - voltageCursor2.getPosition());

    onUpdate(timeCursorDiff, voltCursorDiff);
  });

  Paper.view.on('mouseup', function (event: any) {
    timeCursor1.onMouseUp(event);
    timeCursor2.onMouseUp(event);
    voltageCursor1.onMouseUp(event);
    voltageCursor2.onMouseUp(event);
  });

  
  Paper.view.on('resize', function (event: any) {
    cursorLayer.activate();
    
    timeCursor1.onResize(event, cursorFlag);
    voltageCursor1.onResize(event, cursorFlag);
    timeCursor2.onResize(event, cursorFlag);
    voltageCursor2.onResize(event, cursorFlag);
  });

  Paper.view.on('frame', function (event: any) {
    timeCursor1.setVisible(cursorFlag);
    timeCursor2.setVisible(cursorFlag);
    voltageCursor1.setVisible(cursorFlag);
    voltageCursor2.setVisible(cursorFlag);
  });

}

export default drawCursors;