import React, {useRef, useState} from 'react';
import { isPlatform, IonButtons, IonContent, IonHeader, IonMenuButton, IonPage, IonTitle, IonToolbar,
  IonButton, IonPopover, IonInput } from '@ionic/react';
import './SpectrumAnalyzer.css';
import Canvas from 'components/Canvas';
import WakeLock from 'components/WakeLock';

const SpectrumAnalyzer: React.FC = () => {
  const name = "Spectrum Analyzer";
  const POINTS_DIV = 20;
  const NUM_FREQ_DIVS = 10;
  const NUM_DECIBEL_DIVS = 8;
  const SAMPLE_RATE = 44100;
  const NYQUIST_FREQ = SAMPLE_RATE / 2;
  const minCenter = 0;
  const maxCenter = 20000;
  
  const [flag, setFlag] = useState(true);
  const [fullscreenFlag, setFullscreenFlag] = useState(true);
  const [displayFlag, setDisplayFlag] = useState(true);
  const [decibelsDiv, setDecibelsDiv] = useState(25.0);
  const [freqCount, setFreqCount] = useState(1);
  const [fftSize, setFFTSize] = useState(4096);
  const [freqDiv, setFreqDiv] = useState(POINTS_DIV * (SAMPLE_RATE / fftSize));
  const [center, setCenter] = useState(freqDiv * 5);
  const [windowType, setWindowType] = useState("Rectangle");
  const [cursorFlag, setCursorFlag] = useState(false);
  const [autoset, setAutoset] = useState(false);
  const [freqDiff, setFreqDiff] = useState(0);
  const [voltDiff, setVoltDiff] = useState(0);
  const [saveFlag, setSaveFlag] = useState(false);
  

  function changeCursorDiff(freqDiff: number, voltDiff: number){
    setFreqDiff(freqDiff);
    setVoltDiff(voltDiff);
  }

  function changeCenter(center : number) {
    setCenter(center);
  }

  function changeFreqInc(){
    if(NYQUIST_FREQ > ( freqDiv * freqCount * NUM_FREQ_DIVS) ){
      setFreqCount((prev) => prev+1)
    }
  }
  
  function changeFreqDec(){
    if(freqCount > 1){
      setFreqCount((prev) => prev-1)
    }
  }

  function changeFreqDiv(freqDiv : number) {
    setFreqCount(freqDiv)
  }

  function changeFlag(){
    setFlag(!flag);
  }

  function changeDisplayFlag(){
    setDisplayFlag(!displayFlag);
  }

  function changeFullscreenFlag(){
    setFullscreenFlag(!fullscreenFlag);
  }

  function changeCursorFlag(){
    setCursorFlag(!cursorFlag)
  }

  function toggleSaveButton() {
    // Don't allow toggling off via button.
    setSaveFlag(true);
  }

  function saveFinish() {
    setSaveFlag(false);
  }

  const [fftSizeMenuPopoverState, setShowFFTSizeMenuPopover] = useState({ showPopover: false });
  const [centerPopoverState, setShowCenterPopover] = useState({ showPopover: false, center: 0 });
  const [windowPopoverState, setShowWindowPopover] = useState({ showPopover: false, windowType: windowType });

  function popoverCenterSet() {
    if (minCenter <= centerPopoverState.center && centerPopoverState.center <= maxCenter) {
      setCenter(centerPopoverState.center);
      console.log("Center has been set to: " + centerPopoverState.center + "hz");
      setShowCenterPopover({ showPopover: false, center: center });
    } else {
      inputCenter.current.value = "";
    }
  }

  function toggleAutosetButton() {
    // Don't allow toggling off via button.
    setAutoset(true);
  }

  function autosetFinish() {
    setAutoset(false);
  }
  
  function popoverFFTSizeMenuSet(value: number) {
    // Number given in this case is the number of datapoints for the FFT (powers of 2).
    setShowFFTSizeMenuPopover({ showPopover: false });
    const freqPerPoint = SAMPLE_RATE / value;
    const freqDiv = POINTS_DIV * freqPerPoint;
    setFreqDiv(freqDiv);
    setCenter(freqDiv * (NUM_FREQ_DIVS / 2));
    setFFTSize(value);
    console.log("FFT Size has been set to: " + value);
  }

  function popoverWindowSet(value: string) {
    // Number given in this case (for now and should be temporary) is a string. Ex: 'rectangle'.
    setShowWindowPopover({ showPopover: false, windowType: value });
    setWindowType(value);
    console.log("Window type has been set to: " + value);
  }

  // Setting focus of the center popover
  const inputCenter = useRef<HTMLIonInputElement>(null!);
  function centerInputFocus() {
    inputCenter.current.setFocus();
  }

  function keyUpHandler(event: React.KeyboardEvent<HTMLIonInputElement>, popover: string) {
    if (event.key === "Enter") {
      switch (popover) {
        case "center": popoverCenterSet(); break;
      }
    }
  }

  function calculateDecibels() {
    return (decibelsDiv*NUM_DECIBEL_DIVS*voltDiff).toFixed(1)
  }
  function calculateFrequency(){
    return (freqCount*freqDiv*NUM_FREQ_DIVS*freqDiff).toFixed(3)
  }
  function calculateFrequencyDivision(){
    return (freqCount*freqDiv).toFixed(3)
  }
  function calculateDecibelsDivision(){
    return decibelsDiv.toFixed(1)
  }
  

  return (
    <IonPage>
      <IonHeader className={fullscreenFlag ? "header" : "header-full"}>
        {/* Button for disabling fullscreen */}
        { <IonButton className={fullscreenFlag ? "flscrn-button-graph-small" : "flscrn-button-hidden"} mode="ios" fill="clear" onClick = {() => changeFullscreenFlag()}>
          <img className="fullscreen-img" src={process.env.PUBLIC_URL + "/assets/fullscreenwhite.png"} alt=""></img>
        </IonButton> }
        <IonToolbar mode="ios" color="primary">
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle> <b style={{fontWeight: "bolder"}}>Husker Scope</b>{": " + name}</IonTitle>
        </IonToolbar>
      </IonHeader>

      <IonContent fullscreen scrollY={false} color="secondary">
      <div className="full-screen">

        {/* The button for enabling fullscreen */}
        <IonButton className={fullscreenFlag ? "flscrn-button-hidden" : "flscrn-button-graph-full"} mode="ios" 
                   onClick = {() => changeFullscreenFlag()} fill="clear">
          <img className="fullscreen-img" src={process.env.PUBLIC_URL + "/assets/fullscreen2.png"} alt=""></img>
        </IonButton>

        {/* Button for toggling display on fullscreen */}
        <IonButton className={fullscreenFlag ? "fullscreen-display-button-hidden" : "fullscreen-display-button" } 
                   onClick = {() => changeDisplayFlag()} mode="ios" fill="clear">
          <img className="display-icon" src={process.env.PUBLIC_URL + "/assets/displayoff.png"} alt=""></img>
        </IonButton>


        {/* Display textbox for displaying frequency, Frequency Division, & Voltage Division */}
        <label className={isPlatform("ios") || isPlatform("android") ? (displayFlag ? "display-data-hidden" : (fullscreenFlag ? "display-data-hidden" : "display-data-full")) : 
                                                                       (displayFlag ? "display-data-hidden" : (fullscreenFlag ? "display-data-hidden" : "display-data-full-web"))}>
          {/* <b>Frequency</b>: {frequency}Hz <br /> */}
          <b> Freq Division</b>: {calculateFrequencyDivision()}Hz <br />
          <b> Decibels Division</b>: {calculateDecibelsDivision()}dB <br />
          <b> Center</b>: {center.toFixed(3)}Hz <br />
          <b> Window</b>: {windowType} <br />
          <b> FFT Size</b>: {fftSize} <br />

          { cursorFlag ?
              <>
                <b> Decibels</b>: {calculateDecibels()}dB <br />
                <b> Frequency</b>: {calculateFrequency()}Hz <br />
              </>
              : null
          } 
        </label>

        {/* Makes it so the screen doesn't turn off when on the oscope page */}
        <WakeLock enabled={true} />

        {/* The graph element for the spectrum analyzer */}
        <div className={isPlatform("ios") || isPlatform("android") ? (fullscreenFlag ? "oscilloscope-div" : "oscilloscope-div-full") : 
                                                                     (fullscreenFlag ? "oscilloscope-div-web" : "oscilloscope-div-full-web")}>
        <Canvas 
          className="spectrum-graph"
          isFullscreen={fullscreenFlag} 
          playPauseFlag = {flag}
          decibelsDiv = {decibelsDiv}
          freqDiv = {freqCount}
          cursorChangeCallback = {changeCursorDiff}
          XAxisDivChangeCallback = {changeFreqDiv}
          centerChangeCallback = {changeCenter}
          fftSize = {fftSize}
          autoset = {autoset}
          autosetFinishCallback = {autosetFinish}
          cursorFlag = {cursorFlag}
          saveFlag = {saveFlag}
          saveFinishCallback = {saveFinish}
          windowType = {windowType}
          center = {center}
        />
        </div>

        {/* The buttons on the right hand side of the graph */}
        <div className={isPlatform("ios") || isPlatform("android") ? (fullscreenFlag ? "side-data-sa" : "side-data-hidden") : 
                                                                     (fullscreenFlag ? "side-data-web" : "side-data-hidden")}>
          <div className="first-row-div-sa"> {/* First row of UI buttons */}
            <IonButton class="utility-buttons-sa" mode="ios" onClick = {() => toggleAutosetButton()} color={autoset ? "primary" : "light"} data-testid="autoset-button">
                <div>
                  Auto
                </div>
            </IonButton>
            <IonButton class="voltage-time-division-button-sa" mode="ios" onClick={() => setShowFFTSizeMenuPopover({ showPopover: true })} color="tertiary" data-testid="FFT-size-button">
              <div>
                FFT Size
              </div>
            </IonButton>
            <IonButton class="utility-buttons-sa" mode="ios"  onClick = {() => changeCursorFlag()} color={cursorFlag ? "primary" : "light"} data-testid="cursor-button">
              <div>
                Cursors
              </div>
            </IonButton>
          </div>

          <div className="second-row-div-sa"> {/* Second row of UI buttons */}
          <IonButton class="voltage-time-division-button-sa" mode="ios" onClick = {() => changeFreqDec()} color="tertiary" data-testid="time-div-neg-button">
              <div>
                Freq/Div
                <div>
                  -{freqDiv.toFixed(3)}Hz
                </div>
              </div>
            </IonButton>
            <IonButton id = "pause-play-button-sa" class="pause-play-button-sa" mode="ios" onClick = {() => changeFlag()} color={flag ? "primary" : "tertiary"} data-testid="pause-play-button-sa">
              <div>
                <img className="pause-play-img" src={process.env.PUBLIC_URL + (flag ? "/assets/pause.png" : "/assets/playwhite.png")} alt="" ></img>
                <div>Run/Pause</div>
              </div>
            </IonButton>
            <IonButton class="voltage-time-division-button-sa" mode="ios" onClick = {() => changeFreqInc()} color="tertiary" data-testid="time-div-pos-button">
              <div>
                Freq/Div
                <div>
                  +{freqDiv.toFixed(3)}Hz
                </div>
              </div>
            </IonButton>
          </div>

          <div className="third-row-div-sa"> {/* Third row of UI buttons */}
            <IonButton class="utility-buttons-sa" mode="ios" onClick = {() => toggleSaveButton()} color={saveFlag ? "primary" : "light"} data-testid="save-button">
                <div>
                  Save
                </div>
            </IonButton>
            <IonButton class="voltage-time-division-button-sa" mode="ios" onClick={() => setShowCenterPopover({ showPopover: true, center: center })}  color="tertiary" data-testid="center-button">
              <div>
                Center
              </div>
            </IonButton>
            <IonButton class="utility-buttons-sa" mode="ios" onClick={() => setShowWindowPopover({ showPopover: true, windowType: windowType })} color="light" data-testid="window-button">
              <div>
                Window
              </div>
            </IonButton>
          </div>
          <div className="display-div"> {/* Display Div */}
            <span className="helper" />
            <label className={isPlatform("ios") || isPlatform("android") ? (fullscreenFlag ? "display-data-main" : "display-data-hidden") : 
                                                                           (fullscreenFlag ? "display-data-main-web" : "display-data-hidden")}>
              <b> Freq Division</b>: {calculateFrequencyDivision()}Hz <br />
              <b> Decibels Division</b>: {calculateDecibelsDivision()}dB <br />
              <b> Center</b>: {center.toFixed(3)}Hz <br />
              <b> Window</b>: {windowType} <br />
              <b> FFT Size</b>: {fftSize} <br />
              { cursorFlag ?
                  <>
                    <b> Decibels</b>: {calculateDecibels()}dB <br />
                    <b> Frequency</b>: {calculateFrequency()}Hz <br />
                  </>
                  : null
              } 
            </label>
          </div>
        </div>
      </div>

      {/* Center Popover */}
      <IonPopover
          isOpen={centerPopoverState.showPopover}
          cssClass={isPlatform("ios") || isPlatform("android") ? "input-popover" : "input-popover-web"}
          mode="ios"
          showBackdrop={true}
          onDidPresent={() => centerInputFocus()}
          onDidDismiss={() => setShowCenterPopover({ showPopover: false, center: center })} data-testid="center-popup">
          <div className="popover-content">
            <IonInput ref={inputCenter} class="input-popover-text" type="number" inputmode="numeric" placeholder={minCenter + " to " + maxCenter + " Hz"}
              onIonChange={e => setShowCenterPopover((state) => { return { ...state, center: parseInt(e.detail.value!, 10) } })}
              onKeyUp={e => keyUpHandler(e, "center")}
            ></IonInput>
            <IonButton
              onClick={(e: any) => popoverCenterSet()} data-testid="center-confirm">
              CONFIRM
            </IonButton>
          </div>
        </IonPopover>

        {/* FFT Size Menu Popover */}
        <IonPopover
          isOpen={fftSizeMenuPopoverState.showPopover}
          cssClass={isPlatform("ios") || isPlatform("android") ? "input-popover2" : "input-popover2-web"}
          mode="ios"
          showBackdrop={true}
          onDidDismiss={() => setShowFFTSizeMenuPopover({ showPopover: false })} data-testid="triggerMenu-popup">
          <div><label className="CID">CURRENTLY IN DEVELOPMENT</label></div>
          <div className="popover-content2" style={{marginBottom: "0px"}}>
            <IonButton class="trigger-popup-button"
              onClick={(e: any) => popoverFFTSizeMenuSet(128)} data-testid="Rising-confirm">
              128
            </IonButton>
            <IonButton class="trigger-popup-button"
              onClick={(e: any) => popoverFFTSizeMenuSet(256)} data-testid="Falling-confirm">
              256
            </IonButton>
            <IonButton class="trigger-popup-button"
              onClick={(e: any) => popoverFFTSizeMenuSet(512)} data-testid="Auto-confirm">
              512
            </IonButton>
          </div>
          <div className="popover-content2" style={{marginTop: "0px"}}>
            <IonButton class="trigger-popup-button"
              onClick={(e: any) => popoverFFTSizeMenuSet(1024)} data-testid="Auto-confirm">
              1024
            </IonButton>
            <IonButton class="trigger-popup-button"
              onClick={(e: any) => popoverFFTSizeMenuSet(2048)} data-testid="Auto-confirm">
              2048
            </IonButton>
            <IonButton class="trigger-popup-button"
              onClick={(e: any) => popoverFFTSizeMenuSet(4096)} data-testid="Auto-confirm">
              4096
            </IonButton>
          </div>
        </IonPopover>

        {/* Window Menu Popover */}
        <IonPopover
          isOpen={windowPopoverState.showPopover}
          cssClass={isPlatform("ios") || isPlatform("android") ? "input-popover2" : "input-popover2-web"}
          mode="ios"
          showBackdrop={true}
          onDidDismiss={() => setShowWindowPopover({ showPopover: false, windowType: windowType })} data-testid="triggerMenu-popup">
          <div className="popover-content2" style = {{marginBottom: "0px"}}>
            <IonButton class="trigger-popup-button"
              onClick={(e: any) => popoverWindowSet("Rectangle")} data-testid="rectangle-confirm">
              Rectangle
            </IonButton>
            <IonButton class="trigger-popup-button"
              onClick={(e: any) => popoverWindowSet("Hanning")} data-testid="hanning-confirm">
              Hanning
            </IonButton>
            <IonButton class="trigger-popup-button"
              onClick={(e: any) => popoverWindowSet("Gaussian")} data-testid="gaussian-confirm">
              Gaussian
            </IonButton>
          </div>
          <div className="popover-content2">
            <IonButton class="trigger-popup-button"
              onClick={(e: any) => popoverWindowSet("Hamming")} data-testid="hamming-confirm">
              Hamming
            </IonButton>
            <IonButton class="trigger-popup-button"
              onClick={(e: any) => popoverWindowSet("Kaiser")} data-testid="kaiser-confirm">
              Kaiser
            </IonButton>
            <IonButton class="trigger-popup-button"
              onClick={(e: any) => popoverWindowSet("Flat Top")} data-testid="flattop-confirm">
              Flat Top
            </IonButton>
          </div>
        </IonPopover>
      </IonContent>
    </IonPage>
  );
};

export default SpectrumAnalyzer;
