import React, { useRef, useEffect } from 'react';
import Paper from 'paper';
import drawGraph from 'components/DrawGraph';
import drawTriggers from 'components/triggers';
import drawCursors, {setCursorFlag} from 'components/cursors';
import { drawSpectrumWave, setWindowType, setCenter, setSpectrumDrawWave, setDecibelsDiv, setFreqDiv, setSpectrumAutoset, setSpectrumSaveFlag, setFFTSize } from './DrawSpectrumWave';
import { drawOscopeWave, setTriggers, setTriggerFlag, setOscopeDrawWave, setVoltageDiv, setTimeDiv, setOscopeAutoset, setOscopeSaveFlag } from './DrawOscopeWave';
import { isPlatform } from '@ionic/react';

/* global */

const Canvas = props => {
  
  const canvasRef = useRef(null);
  const renderFrame = useRef(null);
  const isSpectrumGraph = props.className === 'spectrum-graph';

  const animate = () => {
    if(canvasRef.current !== null && canvasRef.current.clientWidth !== 0) {
      const canvas = canvasRef.current;
      Paper.setup(canvas);

      drawGraph(isSpectrumGraph);
      if (!isSpectrumGraph) {
        drawTriggers((val1, val2) => setTriggers(val1, val2));
        drawOscopeWave(
          props.frequencyChangeCallback, 
          props.autosetFinishCallback, 
          props.XAxisDivChangeCallback, 
          props.voltageDivChangeCallback, 
          props.saveFinishCallback
        );
      } else {
        drawSpectrumWave( 
          props.autosetFinishCallback, 
          props.XAxisDivChangeCallback,
          props.centerChangeCallback, 
          props.saveFinishCallback
        );
      }
      drawCursors(props.cursorChangeCallback);

      Paper.view.draw();
    } else {
      renderFrame.current = requestAnimationFrame(animate);
    }
  };

  useEffect(() => {
    renderFrame.current = requestAnimationFrame(animate);

    return () => {
      cancelAnimationFrame(renderFrame.current);
    };
  }, []);

  useEffect(() => {
    // If we have a view, fix the scaling on fullscreen toggle
    if(Paper.view) {
      Paper.view.viewSize = isPlatform("ios") || isPlatform("android") ? new Paper.Size(canvasRef.current.clientWidth, canvasRef.current.clientHeight) : 
                                                                         new Paper.Size(canvasRef.current.clientWidth*2, canvasRef.current.clientHeight*2);
      Paper.view.emit('resize', {});
    }
  }, [props.isFullscreen]);

  // Shared
  useEffect(() => {
    setOscopeDrawWave(props.playPauseFlag);
    setSpectrumDrawWave(props.playPauseFlag);
  }, [props.playPauseFlag]);

  useEffect(() => {
    setOscopeAutoset(props.autoset);
    setSpectrumAutoset(props.autoset);
  }, [props.autoset]);

  useEffect(() => {
    setTriggerFlag(props.triggerFlag);
  }, [props.triggerFlag]);

  useEffect(() => {
    setCursorFlag(props.cursorFlag);
  }, [props.cursorFlag]);

  useEffect(() => {
    setOscopeSaveFlag(props.saveFlag);
    setSpectrumSaveFlag(props.saveFlag);
  }, [props.saveFlag]);

  // Oscope
  useEffect(() => {
    setVoltageDiv(props.voltageDiv);
  }, [props.voltageDiv]);

  useEffect(() => {
    setTimeDiv(props.timeDiv);
  }, [props.timeDiv]);

  // Spectrum Analyzer
  useEffect(() => {
    setDecibelsDiv(props.decibelsDiv);
  }, [props.decibelsDiv]);

  useEffect(() => {
    setFreqDiv(props.freqDiv);
  }, [props.freqDiv]);

  useEffect(() => {
    setWindowType(props.windowType);
  }, [props.windowType]);

  useEffect(() => {
    setCenter(props.center);
  }, [props.center]);

  useEffect(() => {
    setFFTSize(props.fftSize);
  }, [props.fftSize]);
  
  return <canvas ref={canvasRef} className={props.className} resize="true" id="canvas" />
}

export default Canvas;
